import * as React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import BackgroundImage from 'gatsby-background-image'

import Layout from "../components/layout"
import FullScreenSection from "../components/full-screen-section"
import Seo from "../components/seo"

import styled from 'styled-components'

const IndexPage = () => {

  const data = useStaticQuery(
    graphql`
      query {
        desktop: file(relativePath: { eq: "pointcloud.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 1920) {
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }
      }
    `
  )

  const imageData = data.desktop.childImageSharp.fluid

  return (
    <Layout>
      <Seo title="Home" />

      <FullScreenSection>

        <BackgroundImage
          Tag="section"
          className={'container-fluid h-100'}
          fluid={imageData}
          backgroundColor={`#040e18`}
        >

            <div className={`row h-100`}>
              <div className={`col align-self-end pb-3`}>
                <StaticImage
                  alt={`Curva de la Luz`}
                  src="../images/txt.png"
                  width={600}
                  quality={95}
                  style={{ 
                    // paddingBottom: `100px`,
                  }}
                />
              </div>
              <div className={`col-2 align-self-end pb-3`}>
                <StaticImage
                  alt={`Curva de la Luz`}
                  src="../images/fondart.png"
                  width={600}
                  quality={95}
                  style={{ 
                  }}
                />
              </div>          
            </div>

        </BackgroundImage>

      </FullScreenSection>

    </Layout>
  )

}

const StyledBackgroundSection = styled(IndexPage)`
  width: 100%;
  background-position: bottom center;
  background-repeat: repeat-y;
  background-size: cover;
`

export default IndexPage
